.hero-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 50rem;
  will-change: transform;
}

.hero__image {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: relative;
  background-image: url("../images/hero-v2.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 50rem;
  padding-bottom: 0.3125rem;
}
.hero__text {
  width: 95%;
  display: flex;
  flex-direction: column;
  color: hsl(4, 71%, 31%);
  font-size: 15rem;
  font-weight: 500;
}
.hero .khalil {
  align-self: flex-start;
  line-height: 1;
  margin: 0;
  letter-spacing: 5rem;
}
.hero .fong-line {
  align-self: flex-end;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  line-height: 1;
  margin: 0;
  letter-spacing: 0.625rem;
}
.hero .fangdatong {
  display: flex;
  flex-direction: column;
  font-family: "Noto Sans SC", sans-serif;
  font-size: 1.875rem;
}
.hero .fangdatong span:first-child {
  margin-bottom: 3.75rem;
}
.hero .fangdatong span {
  margin-bottom: 0.625rem;
}
.hero .fangdatong span:last-child {
  margin-bottom: 0;
}
.hero .fong {
  letter-spacing: 0.625rem;
}

.hero-spacer {
  height: 50rem;
}

.wish-wrapper {
  position: relative;
  inset: 0;
  margin: auto;
  margin-top: 6.25rem;
  margin-bottom: 6.25rem;
  width: 18.75rem;
  height: 9.375rem;
}
.wish-wrapper p {
  position: relative;
  text-align: center;
  letter-spacing: 1.25rem;
  font-family: "Noto Sans SC", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  margin: 0;
  margin-bottom: 1.875rem;
  padding: 0;
  z-index: 100;
}
.wish-wrapper p:nth-child(2) {
  color: hsl(4, 71%, 31%);
}
.wish-wrapper .line2 {
  margin-top: 1.875rem;
  margin-left: 3.125rem;
}

.stripe-overlay {
  position: absolute;
  inset: 0;
  background: repeating-linear-gradient(to right, black, black 1.875rem, transparent 1.875rem, transparent 3.75rem);
  pointer-events: none;
  z-index: 1000;
}

.about__1 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  margin-bottom: 6.25rem;
  gap: 1.875rem;
}
.about__1__wrapper {
  max-width: 37.5rem;
}
.about__1__text {
  max-width: 15.5rem;
  padding-bottom: 5.3125rem;
  margin-right: 5rem;
}
.about__1__text h2 {
  margin-bottom: 3.75rem;
}

.slider-wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  border-radius: 0.3125rem;
}

.slider-track {
  display: flex;
  width: 400%;
  transition: transform ease-in-out 0.5s;
}

.slide {
  flex: 0 0 25%;
  max-width: 25%;
}
.slide img {
  display: block;
  width: 100%;
  height: 25rem;
  object-fit: cover;
  border-radius: 0.3125rem;
}

.slider-arrow {
  display: flex;
  position: relative;
  justify-content: space-between;
  width: 7.5rem;
  margin: 1.25rem auto;
  pointer-events: auto;
  user-select: none;
}
.slider-arrow button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
  width: 2.5rem;
  height: 1.5rem;
  color: white;
  transition: color 500ms ease-in-out;
}
.slider-arrow button:hover, .slider-arrow button:focus {
  color: hsl(4, 71%, 31%);
}

.about__2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5.625rem;
  align-items: center;
  margin-bottom: 9.375rem;
}
.about__2 .col {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
.about__2 .right {
  margin-left: 3.125rem;
  max-width: 28.125rem;
}
.about__2 img {
  display: block;
  max-width: auto;
  height: 25rem;
  border-radius: 0.3125rem;
}
.about__2 .text {
  max-width: 17.5rem;
  width: 100%;
}

.about__3 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0;
  margin-bottom: 9.375rem;
  gap: 1.875rem;
}
.about__3__wrapper {
  max-width: 20rem;
}
.about__3__text {
  max-width: 19.375rem;
  padding-bottom: 5.3125rem;
  padding-left: 1.25rem;
  margin-right: 5rem;
}
.about__3__text p:nth-child(1) {
  margin-bottom: 3.75rem;
}

.lyrics {
  overflow: auto;
  perspective: 1px;
  transform-style: preserve-3d;
}
.lyrics > * {
  position: absolute;
}
.lyrics,
.lyrics * {
  transform-style: preserve-3d;
}

.lyrics {
  height: 18.75rem;
  margin-top: 12.5rem;
  margin-bottom: 12.5rem;
  mask: linear-gradient(0deg, transparent, white 10%, white 80%, transparent);
}
.lyrics h3 {
  font-family: "Noto Sans SC", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
}
.lyrics :nth-child(1) {
  transform: translateZ(-0.4px) scale(1.4);
  z-index: -400;
  top: 12.5rem;
  left: 12.5rem;
}
.lyrics :nth-child(2) {
  transform: translateZ(0.2px) scale(0.8);
  z-index: 200;
  top: 25rem;
  left: 50rem;
}
.lyrics :nth-child(3) {
  transform: translateZ(0.1px) scale(0.9);
  z-index: 100;
  top: 37.5rem;
  left: 56.25rem;
}
.lyrics :nth-child(4) {
  transform: translateZ(-0.1px) scale(1.1);
  z-index: -100;
  top: 43.75rem;
  left: 6.25rem;
}
.lyrics :nth-child(5) {
  transform: translateZ(-2px) scale(3);
  z-index: -2000;
  top: 62.5rem;
  left: 25rem;
}
.lyrics :nth-child(6) {
  transform: translateZ(-0.5px) scale(1.5);
  z-index: -500;
  top: 75rem;
  left: 6.25rem;
}
.lyrics :nth-child(7) {
  transform: translateZ(-1px) scale(2);
  z-index: -1000;
  top: 56.25rem;
  left: 56.25rem;
}
.lyrics :nth-child(8) {
  transform: translateZ(-0.3px) scale(1.3);
  z-index: -300;
  top: 31.25rem;
  left: 6.25rem;
}
.lyrics :nth-child(9) {
  transform: translateZ(-0.1px) scale(1.1);
  z-index: -100;
  top: 56.25rem;
  left: 12.5rem;
}
.lyrics :nth-child(10) {
  transform: translateZ(-1px) scale(2);
  z-index: -1000;
  top: 37.5rem;
  left: 37.5rem;
}

.albums {
  margin-bottom: 12.5rem;
}
.albums h2 {
  text-align: center;
}

.album-player {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100vw;
  max-width: 100%;
  height: 50rem;
  box-sizing: border-box;
  transition: all 0.5s ease-in-out;
}

.left-side {
  position: relative;
  flex: 0 0 25%;
  padding-left: 1.875rem;
}
.left-side .menu {
  list-style: none;
  line-height: 2.8;
}
.left-side #menu-indicator {
  position: absolute;
  left: 0;
  width: 0.25rem;
  height: 1.25rem;
  background-color: currentColor;
  transition: top 0.5s ease-in-out;
}

.middle {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-right: 4rem;
}
.middle img {
  display: block;
  object-fit: cover;
  width: 37.5rem;
  height: 37.5rem;
  border-radius: 0.1875rem;
}
.middle h3 {
  text-align: center;
  margin: 1.25rem 0 0 0;
  font-size: 1.875rem;
  letter-spacing: 0.5rem;
}

.right-side {
  height: 30rem;
  flex: 0 0 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 0;
}
.right-side #album-info {
  margin: 0 0 1.875rem 0;
}
.right-side #album-songs .jtw-wrapper {
  display: flex;
  flex-direction: row;
  gap: 0.625rem;
}
.right-side #album-songs .disc {
  flex: 1;
}
.right-side #album-songs p {
  font-size: 0.875rem;
  margin: 0;
  padding: 0;
}

