.photowall {
  margin-bottom: 9.375rem;
}

.scroller {
  width: 100%;
  margin-bottom: 1.875rem;
}
.scroller__inner {
  display: flex;
  height: 11.25rem;
  gap: 7.5rem;
}
.scroller__inner img {
  height: 100%;
  pointer-events: none;
  border-radius: 0.1875rem;
}

.scroller[data-animated=true] {
  overflow: hidden;
  mask: linear-gradient(90deg, transparent, white 10%, white 90%, transparent);
}
.scroller[data-animated=true] .scroller__inner {
  width: max-content;
  flex-wrap: nowrap;
  animation: scroll var(--_animation-duration, 60s) var(--_animation-direction, forwards) linear infinite;
}

.scroller[data-speed=fast] {
  --_animation-duration: 30s;
}

.scroller[data-speed=slow] {
  --_animation-duration: 30s;
}

@keyframes scroll {
  to {
    transform: translate(calc(-50% - 3.75rem));
  }
}
.gallery {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1.875rem;
  margin-bottom: 12.5rem;
}

.gallery-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5.625rem;
  max-width: 75rem;
  width: 100%;
  --gif-texture: url(https://64.media.tumblr.com/da60c13b478dda09ab90c27e880983b8/tumblr_nd4pwdPKdc1tun3l0o1_1280.gifv);
}
.gallery-wrapper a {
  display: block;
  position: relative;
  overflow: hidden;
  height: 12.5rem;
  border-radius: 0.3125rem;
  margin: 0;
  padding: 0;
}
.gallery-wrapper a:hover::after {
  content: "";
  position: absolute;
  inset: 0;
  background-image: var(--gif-texture);
  background-size: cover;
  background-position: center;
  mix-blend-mode: overlay;
  opacity: 0.3;
  pointer-events: none;
}
.gallery-wrapper img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease-in-out;
}
.gallery-wrapper img:hover {
  transform: scale(1.05);
}

.quote-wrapper {
  height: 12.5rem;
  display: grid;
  place-items: center;
  margin-top: 18.75rem;
  margin-bottom: 18.75rem;
}

.quote {
  width: 68.75rem;
  overflow: hidden;
  white-space: normal;
  font-family: "Noto Sans SC", sans-serif;
  font-size: 1rem;
  line-height: 3;
  letter-spacing: 0.3125rem;
}

.cursor {
  display: inline-block;
  width: 0.0625rem;
  height: 1.125rem;
  background: white;
  animation: blink 0.7s step-end infinite;
  vertical-align: middle;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
footer {
  text-align: center;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  background-color: black;
  min-height: 125rem;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  overflow-x: hidden;
  margin: 0 auto;
  padding: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: white;
  line-height: 1.5;
}
body.noscroll {
  overflow: hidden;
}

.content {
  position: relative;
  background: black;
  padding: 3.125rem;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
}

h2 {
  font-size: 3rem;
  font-weight: 400;
  letter-spacing: 0.5rem;
}

p {
  font-size: 0.875rem;
  line-height: 1.8;
}